// @flow strict
import React from 'react';
import { Link } from 'gatsby';
import Author from './Author';
import Comments from './Comments';
import Content from './Content';
import Meta from './Meta';
import Tags from './Tags';
import styles from './Post.module.scss';
import type { Node } from '../../types';
import ReactDisqusComments from 'react-disqus-comments';

type Props = {
  post: Node
};

const Post = ({ post }: Props) => {
  const { html } = post;
  const { tagSlugs, slug, categorySlug } = post.fields;
  const { tags, title, date, category } = post.frontmatter;

  return (
    <div className={styles['post']}>
      <Link className={styles['post__home-button']} to="/">Inicio</Link>

      <div className={styles['post__content']}>
        <Content body={html} title={title} category={category} categorySlug={categorySlug} />
      </div>

      <div className={styles['post__footer']}>
        <Meta date={date} />
        {tags && tagSlugs && <Tags tags={tags} tagSlugs={tagSlugs} />}
        <Author />
      </div>

      <div className={styles['post__comments']}>
        <h3>Comentarios</h3>
        <Comments postSlug={slug} postTitle={post.frontmatter.title} />
        <ReactDisqusComments
          shortname="fabian-sanhueza"
          identifier={post.postSlug}
          title={post.title}
          url={post.url}
          category_id={post.category_id}
        />
      </div>
    </div>
  );
};

export default Post;
